/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
    display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
    display: none;
}

body {
    line-height: 1;
}

input {
    border: none;
    outline: none;
}

menu,
ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Template styling */
body {
    color: #F8EDE3;
    font-family: "Lato", sans-serif;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
}


header,
nav {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4rem;
    height: 80px;

    width: 100vw;

    background: rgba(0, 0, 0, .5);
}

nav div {
    cursor: pointer;
}

/* Home-Page Styling */

.home-page {
    margin-top: 25vh;
}

.blurb-ctr {
    width: 40vw;
    display: flex;
    flex-direction: column;
    gap: 40px;
    padding: 30px;

    background: rgba(0, 0, 0, .5);
}

.home-page h2 {
    font-size: 3rem;
    font-weight: 600;

    align-self: flex-end;
}

