.menu-page h1 {
    font-size: 3rem;
    margin-bottom: 5rem;
}

.menu-page h2 {
    font-size: 2rem;
}

.menu-ctr {
    font-size: 1.3rem;
    display: grid;
    grid-template-columns: repeat(2, minmax(300px, 1fr));
    grid-auto-rows: minmax(300px, 600px);
}

.menu-ctr>div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.menu-page img {
    align-self: center;
    height: 25rem;
    width: 20rem;
}

.drink-item ul {
    display: grid;
    grid-template: 1fr 1fr/ 1fr 1fr
}

