.contact-page {
    width: 75vw;
    display: grid;
    grid-template: 1fr 3fr/ 1fr 1fr 1fr;
    margin-top: 80px;
}

.contact-page h2 {
    font-size: 2rem;
}

form {
    grid-area: 2/ 3/3/4;
}

.map-ctr {
    grid-area: 2/1/3/3;
}

